export function approx(value) {
  if (value != null){return value.toFixed(2);}
  return 0; 
}
export function isTest() {
  if (window.location.origin.indexOf("3000")>-1) return true;
  if (window.location.origin.indexOf("amazonaws.com")>-1) return true; //minor hack while deploying client to aws
  return false;
}

export function frequency(ar){
  //take an array of arrays where it is [[time, value], [time, value].....
  //Measure each time value goes from down to up as the frequency and average them out
  let calc = new Array(0);
  let last_time = 0;
  let dir='down';
  if ((ar[1][1]-ar[0][1])>0) {dir='up'}
  for (let i=2; i<ar.length; i++){
    if ((ar[i][1]-ar[i-1][1])>0) {
      dir='up'
    } else {
      if (dir === 'up') {
        if (last_time === 0) {
          last_time=ar[i][0]
        } else {
          calc.push(last_time=ar[i][0]-last_time);
          last_time=ar[i][0];
        }
      }
      dir='down'; //set it to down and change it if it is up
    }
  }
  let tot=0;
  for (let i=0; i<calc.length; i++){
    tot+=calc[i];
  }
 let average = tot/(1000*calc.length);
  return (1/average).toFixed(2);
}