import React, { useEffect } from 'react';
import { frequency } from './utils';
/*
All from https://sensor-js.xyz/demo.html
*/
const TremorApp = ({showGraph}) => {

  useEffect(() => {
    window.google.charts.load('current', {packages: ['corechart', 'line']});
  });

  const drawBasic = (chart_id, col, rows) => {
    var data = new window.google.visualization.DataTable();
    data.addColumn('number', 'Time');
    data.addColumn('number', col);

    data.addRows(rows);

    var options = {
      hAxis: {title: 'Time (ms)'},
      vAxis: {title: col}};
    var chart = new window.google.visualization.LineChart(document.getElementById(chart_id));
    chart.draw(data, options);
  }

  const drawGraphs = () => {
    if (showGraph){
      drawBasic('base_chart_x', 'x', window.graphX);
      document.getElementById("freq_x").innerHTML = frequency(window.graphX);
      drawBasic('base_chart_y', 'y', window.graphY);
      document.getElementById("freq_y").innerHTML = frequency(window.graphY);
      document.getElementById("info").innerHTML = window.graphY.toString();
    } else {
      try {
        document.getElementById("base_chart_y").innerHTML = '';
        document.getElementById("freq_y").innerHTML = '';
        document.getElementById("base_chart_x").innerHTML = '';
        document.getElementById("freq_x").innerHTML = '';
        document.getElementById("info").innerHTML = '';
      } catch {}
    }
  }

  return (
    <>
      <div id="base_chart_x"></div>
      <div id="freq_x"></div>
      <div id="base_chart_y"></div>
      <div id="freq_y"></div>
      <div id="info"></div>
      {drawGraphs()}
    </>    
  );
}
export default TremorApp;